.btn,
.btn:link,
.btn:visited {
  text-transform: uppercase;
  text-decoration: none;
  /* get off the underline */
  padding: 1.5rem 4rem;
  display: inline-block;
  /* if you want the padding and height of width workds out */
  border-radius: 10rem;
  transition: all 0.2s;
  /* all the properties are enabled to be animated, always put the transition properties on the intial states */
  /* position: relative; */
  /* in this way the btn:after will not reference the header but the btn */
  /* top: 5rem; */
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  margin-bottom: 3rem;
  border: none;
  cursor: pointer;
}
.apply {
  width: 100%;
}
.btn1 {
  text-transform: uppercase;
  text-decoration: none;
  display: flex;
  /* get off the underline */
  padding: 1.5rem 4rem;
  color: white !important;

  /* if you want the padding and height of width workds out */
  border-radius: 10rem;
  transition: all 0.2s;
  /* all the properties are enabled to be animated, always put the transition properties on the intial states */
  /* position: relative; */
  /* in this way the btn:after will not reference the header but the btn */
  /* top: 5rem; */
  justify-content: center;
  text-align: center;
  background-color: #7646ad !important;
  max-width: 20%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3rem;
  border: none;
  cursor: pointer;
}
/* .apply {
  margin-left: auto !important;
  margin-right: auto !important;
} */
.btn1:hover {
  transform: translateY(-0.3rem);
  /* negative means move up, since it moves from up to down  */
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
  /* 0 for x, 10 for y 20px for blur or shadow */
}
.btn1:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  transform: scale(1.5);
  /* increase the size */
  opacity: 0;
}

.btn1:active,
.btn1:focus {
  outline: none;
  transform: translateY(-0.1rem);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

.btn--white {
  background-color: #7646ad;
  color: #777;
  margin-left: auto;
  margin-right: auto;
}
.btn--white::after {
  background-color: #7646ad;
}

.btn1::after {
  /* the intial state for all after */
  content: "";
  /* always necessary */
  display: inline-block;
  height: 100%;
  width: 100%;
  /* pesudo is treated as the child of the button or the object */
  border-radius: 10rem;
  position: absolute;
  top: 0;
  /* left: 0; */
  z-index: -1;
  /* the layer position */
  transition: all 0.4s;
}

.btn--animated {
  animation: moveInBottom 0.5s ease-out 0.75s;
  animation-fill-mode: backwards;
  margin-left: auto;
  margin-right: auto;
  /* the elments will be used brfore the animation starts */
}

.row {
  max-width: 114rem;
  margin: 0 auto;
}
.row:not(:last-child) {
  margin-bottom: 8rem;
}
@media only screen and (max-width: 56.25em) {
  .row:not(:last-child) {
    margin-bottom: 6rem;
  }
}
@media only screen and (max-width: 56.25em) {
  .row {
    max-width: 50rem;
    padding: 0 3rem;
  }
}
.row::after {
  content: "";
  display: table;
  clear: both;
}
.row [class^="col-"] {
  float: left;
}
.row [class^="col-"]:not(:last-child) {
  margin-right: 6rem;
}
@media only screen and (max-width: 56.25em) {
  .row [class^="col-"]:not(:last-child) {
    margin-right: 0;
    margin-bottom: 6rem;
  }
}
@media only screen and (max-width: 56.25em) {
  .row [class^="col-"] {
    width: 100% !important;
  }
}
.row .col-1-of-2 {
  width: calc((100% - 2 * 6rem) / 2);
}
.row .col-1-of-3 {
  width: calc((100% - 3 * 6rem) / 3);
}
.row .col-2-of-3 {
  width: calc(2 * ((100% - 2 * 6rem) / 3) + 6rem);
}
.row .col-1-of-4 {
  width: calc((100% - 3 * 6rem) / 4);
}
.row .col-2-of-4 {
  width: calc(2 * ((100% - 3 * 6rem) / 4) + 6rem);
}
.row .col-3-of-4 {
  width: calc(3 * ((100% - 3 * 6rem) / 4) + 2 * 6rem);
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

